<template>
	<view class="main">
		<view class="nav">
			<view class="nav-list" @click="back"><image :src="fanhuiIcon" mode="" class="backicon"></image></view>
			<view class="nav-list" id="life0">选卡充值</view>
			<view class="nav-list"></view>
		</view>
		<view class="modalbg"  v-if="MoTaiKuang"></view>
		<view class="Mo_Tai_Kuang"  v-if="MoTaiKuang">
			<view class="Mo_Tai_Kuang_top">
				<image :src="Mo_Tai_Kuang_mctd_img" class="Mo_Tai_Kuang_card_image"></image>			  
				<view class="Mo_Tai_Kuang_card">
					<view class="Mo_Tai_Kuang_cardtop">
						<view class="Mo_Tai_Kuang_cardVip">
							VIP
						</view>
						<view class="Mo_Tai_Kuang_cardTypeC">
							{{Mo_Tai_Kuang_mct_name}}
						</view>
					</view>
					<view class="Mo_Tai_Kuang_cardfooter"> 
						<view class="Mo_Tai_Kuang_noCard">
							NO.{{Mo_Tai_Kuang_card_id}}
						</view>
						<view class="Mo_Tai_Kuang_moneny">
							<view class="Mo_Tai_Kuang_moneny1">
								{{Mo_Tai_Kuang_card_name}}
							</view>
							<text class="Mo_Tai_Kuang_moneny2">
								{{Mo_Tai_Kuang_balanceview}}
							</text>
						</view> 
					</view>
				</view>	
				<view  class="zhong_wen_chong_ka">
					请选择充值金额
				</view>
				<view class="Mo_Tai_Kuang_bottom">
					<view  v-for="(chooseCardTypeVal, index) in chooseCardType" :key="index" @click="chongzhi(chooseCardTypeVal,index)" >
						<image :src="chooseCardTypeVal.cCt_mctd_img" mode="" class="Mo_Tai_Kuang_card_image"  :class="{MenuCurrent:cardIndex === index}"></image>
						<view class="Mo_Tai_Kuang_bottom_card">
							<view class="Mo_Tai_Kuang_bottom_cardtop">
								<view class="Mo_Tai_Kuang_bottom_cardVip">
									VIP
								</view>
								<view class="Mo_Tai_Kuang_bottom_cardTypeC">
									{{chooseCardTypeVal.cCt_mct_name}}
								</view>
							</view>
							<view class="Mo_Tai_Kuang_bottom_cardfooter"> 
								<view class="Mo_Tai_Kuang_bottom_noCard">
									NO.{{chooseCardTypeVal.cCt_mct_pkid}}
								</view>
								<view class="Mo_Tai_Kuang_bottom_moneny">
									<view class="Mo_Tai_Kuang_bottom_moneny1">
										{{chooseCardTypeVal.cCt_mctd_name}}
									</view>
									<text class="Mo_Tai_Kuang_bottom_moneny2">
										{{chooseCardTypeVal.chooseCardLeftBot}}元
									</text>
								</view> 
							</view>
						</view>
					</view>	
				</view>
				<view class="Mo_Tai_Kuang_button">
					<view class="Mo_Tai_Kuang_button_quxiao" @click="quxiao">
						取消
					</view>
					<view class="Mo_Tai_Kuang_button_queren" @click="go_chongzhi_success">
						确认
					</view>
				</view>
			</view>	
		</view>
		<view class="shu">
			|
			<view class="dengji huiyuan">会员信息</view>
		</view>
		<view class="huiyuan_xinxi">
			<view class="info">
				<view class="name">
					{{ name }} 
				</view>
				<view class="number">
					{{ number }}
				</view>
			</view>
			<view class="huiyuan_xinxi_image">
				
			</view>			
		</view>
		<!-- 阴影 -->
		<view class="yinying"></view>
		<view class="huiyuanka">
			<view class="shu">
				|
				<view class="dengji huiyuan">会员卡</view>
				
			</view>
			<view class="kabody">
				<view class="huiyuan_kaImg" >
					<view class="huiyuan_kaImg_r" v-for="(imgVal, index) in img" :key="index" @click="goChongZhiJinE(imgVal,index)">
						<image :src="imgVal.mctd_img" mode="" class="card_image"></image>
							<view class="card">
								<view class="cardtop">
									<view class="cardVip">
										VIP
									</view>
									<view class="cardTypeC">
										{{imgVal.mct_name}}
									</view>
								</view>
								<view class="cardfooter"> 
									<view class="noCard">
										NO.{{imgVal.card_id}}
									</view>
									<view class="moneny">
										<view class="moneny1">
											{{imgVal.card_name}}
										</view>
										<text class="moneny2">
											{{imgVal.balanceview}}
										</text>
									</view> 
								</view>
							</view>
							
						</view>
					</view>
				</view>
			</view>
		</view>		
	</view>
	
</template> 

<script>
	/* "use strict"; */
	export default {
		data() { 
			return {
				fanhuiIcon:this.httpUrl+'file/static/order/jiehznag-icon-fanhui.png',
				name:'',
				number:'',
				img:[],
				openid:"oTN2u4tjaIkcskI_peSWmmdbtmbA",
				created_by:'',
				shop_pkid:'',
				mi_pkid:'',
				// 模态框开始
				MoTaiKuang:false,
				Mo_Tai_Kuang_mctd_img:'',
				Mo_Tai_Kuang_mct_name:'',
				Mo_Tai_Kuang_card_id:'',
				Mo_Tai_Kuang_card_name:'',
				Mo_Tai_Kuang_balanceview:'',
				chooseCardType:[],
				// 模态框结束
				guoqi:'0',
				chooseCardLeftBot:'',  // 选择卡时候的左下角显示内容
				imageIndex:'',  //会员卡角标
				chooseCardTypeIndex:'',  // 选卡时的角标
				cardIndex:null,//卡片选择
			}
		},
		onLoad(option) {
			console.log(option)
			let _this = this;
			_this.shop_pkid = option.shop_pkid;
			_this.mi_pkid = option.mi_pkid;
			_this.created_by = option.created_by;
			_this.gethuiyuan();
		},
		methods: {
			// 去充值页面
			go_chongzhi_success(){
				
				let _this = this;
				let chooseCardTypeIndexs = _this.chooseCardTypeIndex; // 选卡时的角标
				let imageIndexs = _this.imageIndex;//会员卡角标
				
				let cardtype = _this.img[imageIndexs].mct_name;//卡的类型
				let cardid =  _this.img[imageIndexs].card_id;//原卡id
				let mc_pkid = _this.img[imageIndexs].mc_pkid;
				let mctpkid = _this.img[imageIndexs].mct_pkid;//会员卡会员主键id
				let numberofuse = _this.img[imageIndexs].number_of_use;//次数
				let cardname = _this.img[imageIndexs].card_name;//卡的有效期
				let mctdmonth = _this.chooseCardType[chooseCardTypeIndexs].cCt_mctd_month;//开通月数
				let shoppkid = _this.shop_pkid;//商店id
				let createdby = _this.created_by;//创建人
				let mctdmoney = _this.chooseCardType[chooseCardTypeIndexs].cCt_mctd_money;//实际的充值金额
				let rruse =  _this.chooseCardType[chooseCardTypeIndexs].cCt_mctd_frequency;//充值次数
				let rrday = _this.chooseCardType[chooseCardTypeIndexs].cCt_mctd_month;//充值天数
				let remark2 =  _this.img[imageIndexs].guoqi;//过没过期
				let balance = _this.chooseCardType[chooseCardTypeIndexs].cCt_mctd_money;//充后余额
				
				let balanceBefore = "";//储值卡原余额
				let numberofuseBefore = _this.img[imageIndexs].number_of_use;//充前次数
				let mctdname = _this.chooseCardType[chooseCardTypeIndexs].cCt_mctd_name;//充值时长
				console.log("cardid",cardid)
				if(cardtype == "储值卡"){
						
					balanceBefore = _this.img[imageIndexs].balance;//原卡的余额
					//获取模态框中储值卡片信息
					mctdmoney = _this.chooseCardType[chooseCardTypeIndexs].chooseCardLeftBot; // 500+500
					balance = Number(mctdmoney) + Number(balanceBefore);  // 充后的余额
				
					console.log(balance);			
				}else if(cardtype == "次数卡"){
					numberofuse = Number(numberofuse)+Number(rruse);//充值次数卡后次数
				}
				uni.navigateTo({
					url:'../chongzhi_jine/chongzhi_jine?mc_pkid='+mc_pkid+"&balance="+balance+"&number_of_use="+numberofuse+"&remark3="+mctdmonth
					+"&card_id="+cardid+"&shop_pkid="+shoppkid+"&created_by="+createdby+"&rr_money="+mctdmoney+"&rr_day="+rrday+"&rr_use="+rruse
					+"&remark2="+remark2+"&cardtype="+cardtype+"&balanceBefore="+balanceBefore+"&numberofuseBefore="+numberofuseBefore+"&mctdname="+mctdname
					+"&cardname="+cardname
				});
				this.MoTaiKuang = false;
				_this.cardIndex = null;
			},
			// 获得选卡时的角标
			chongzhi(chooseCardTypeVal,index){
				let _this = this;
				_this.chooseCardTypeIndex = index;
				_this.cardIndex = index;
				console.log(chooseCardTypeVal)
				console.log("_this.chooseCardTypeIndex",_this.chooseCardTypeIndex)
				
			},
			// 模态框取消 
			quxiao(){
				this.MoTaiKuang = false;
				this.cardIndex = null;
			},
			back(){
				uni.navigateBack({
					delta:1
				})
			},
			gethuiyuan(){
					let _this = this
					_this.shop_pkid = _this.shop_pkid,
					_this.mi_pkid = _this.mi_pkid
					uni.request({
				 		 url: _this.httpUrl + '	Jni_Member_InfoController/findById.xsh',
				 		 method: 'POST',
						 header:{ 'content-type':'application/x-www-form-urlencoded' },
						 data:{
							 shop_pkid:_this.shop_pkid,
							 mi_pkid:_this.mi_pkid
						 },
				 		 success(res){ 
							console.log('res',res)
							let obj1 = res.data.obj_one;
							let obj2 = res.data.obj_two;
						  
							// 会员姓名和会员号
							for (let i=0; i<obj1.length; i++) {
								_this.name=obj1[i].name, 
								_this.number= obj1[i].mi_pkid								
							}
							
							for(let i=0; i<obj2.length; i++){
								let balanceobj = obj2[i].balance+"元";
								let cardname = obj2[i].card_name;
								//获取会员卡图片
								let imgurl = "";
								if(obj2[i].mct_name == '时效卡'){
									balanceobj = "";
									let createddate = obj2[i].created_date;
									createddate = createddate.replace(new RegExp('/','g'),".");
									createddate = createddate.replace(new RegExp('-','g'),".");
									
									let maturitytime = obj2[i].maturity_time;
									maturitytime = maturitytime.replace(new RegExp('/','g'),".");
									maturitytime = maturitytime.replace(new RegExp('-','g'),".");
									
									cardname = "有效期"+createddate+"-"+maturitytime;
									// 是否过期
										let date = new Date();
										let nowyear = date.getFullYear();
										let nowmonth = date.getMonth()+1;
										if(nowmonth < 10){
											nowmonth = "0" + nowmonth;
										}
										let nowdate = date.getDate();
										if(nowdate < 10){
											nowdate = "0"+nowdate
										}
										//当前时间
										let d1 = nowyear+"-"+nowmonth+"-"+nowdate;
										let date1 = new Date(d1.replace(/-/g,"\/"));
										date1 = Date.parse(date1);
										//获取循环出日期
										 let d2 = obj2[i].maturity_time;
										 let date2 = new Date(d2.replace(/-/g,"\/"));
										 date2 = Date.parse(date2);
										//日期比较
										 if(date1>date2){
											imgurl = this.httpUrl+'file/static/jiantouka_tupian/outdate@2x.png';
											_this.guoqi = '1';
										}else{
											imgurl = obj2[i].mctd_img;
											_this.guoqi = '0';
										}
									// 是否过期结束	
								}
								if(obj2[i].mct_name == '储值卡'){
									imgurl = obj2[i].mctd_img;
								}
								if(obj2[i].mct_name == '打折卡'){
									let maturitytime = obj2[i].maturity_time;
									maturitytime = maturitytime.replace(new RegExp('/','g'),".");
									maturitytime = maturitytime.replace(new RegExp('-','g'),".");
									balanceobj = maturitytime +'到期';
									// 是否过期
										let date = new Date();
										let nowyear = date.getFullYear();
										let nowmonth = date.getMonth()+1;
										if(nowmonth < 10){
											nowmonth = "0" + nowmonth;
										}
										let nowdate = date.getDate();
										if(nowdate < 10){
											nowdate = "0"+nowdate
										}
										//当前时间
										let d1 = nowyear+"-"+nowmonth+"-"+nowdate;
										let date1 = new Date(d1.replace(/-/g,"\/"));
										date1 = Date.parse(date1);
										//console.log("时间戳1："+date1);
										//获取循环出日期
										 let d2 = obj2[i].maturity_time;
										 let date2 = new Date(d2.replace(/-/g,"\/"));
										 date2 = Date.parse(date2);
										// console.log("时间戳2："+date2);
										//日期比较
										 if(date1>date2){
											imgurl = this.httpUrl+'file/static/jiantouka_tupian/outdate@2x.png';	
											_this.guoqi = "1";
										}else{
											imgurl = obj2[i].mctd_img;
											_this.guoqi = "0";
										}
									// 是否过期结束	
								}
								
								if(obj2[i].mct_name == '次数卡'){
									balanceobj = obj2[i].number_of_use+"次";
									imgurl = obj2[i].mctd_img;
								}
								
							
								 _this.img.push({
									 guoqi:_this.guoqi,
									 balance: obj2[i].balance,
									 balanceview: balanceobj,
									 card_id : obj2[i].card_id,
									 card_level_discount : obj2[i].mctd_discount,
									 card_name : cardname,
									 default_card : obj2[i].default_card,
									 mctd_img : imgurl,
									 is_used : obj2[i].is_used,
									 maturity_time : obj2[i].maturity_time,
									 mc_pkid : obj2[i].mc_pkid,
									 mct_pkid : obj2[i].mct_pkid,  //会员卡会员主键
									 mi_pkid : obj2[i].mi_pkid,
									 number_of_use : obj2[i].number_of_use,
									 card_id: obj2[i].card_id,
									 mct_name: obj2[i].mct_name,
									 created_date:obj2[i].created_date,
									 is_used:obj2[i].is_used,
								 })		
							}
							console.log("img11111",_this.img)
							
				 		 }, 
				 		 fail(err){
				 			 console.log("响应失败",err)
				 		 }
					})
			},
			// 点击选充值类型
			goChongZhiJinE(imgVal,index){
				let _this = this;
				
				console.log("imgVal",imgVal)
				console.log("index",index)
				_this.imageIndex = index;
				_this.mctd_img = imgVal.mctd_img; 
				_this.card_name = imgVal.card_name;
				_this.balanceview = imgVal.balanceview;
				_this.balance = imgVal.balance;  //余额
				_this.card_id = imgVal.card_id;  //卡号
				_this.card_level_discount = imgVal.card_level_discount;  //折扣
				_this.default_card = imgVal.default_card;  //默认支付
				_this.is_used = imgVal.is_used;  //启用状态
				_this.maturity_time = imgVal.maturity_time;  //到期时间
				_this.mc_pkid = imgVal.mc_pkid;  //会员卡信息表主键
				_this.mct_name = imgVal.mct_name;  //卡种中文
				_this.mct_pkid = imgVal.mct_pkid;  //会员卡类型表主键
				_this.mi_pkid = imgVal.mi_pkid;  //会员信息主键
				_this.number_of_use = imgVal.number_of_use;  //使用次数 
				_this.is_used = imgVal.is_used; // 是否还有卡 0 有卡
				_this.guoqi = imgVal.guoqi;
				console.log("_this.guoqi",_this.guoqi,_this.card_level_discount)
				
				// 模态框的内容
				_this.Mo_Tai_Kuang_mctd_img = _this.mctd_img;
				_this.Mo_Tai_Kuang_mct_name = _this.mct_name;
				_this.Mo_Tai_Kuang_card_id = _this.card_id;
				_this.Mo_Tai_Kuang_card_name = _this.card_name;
				_this.Mo_Tai_Kuang_balanceview = _this.balanceview;
				_this.chooseCardType = [];
				
				if(_this.mct_name == '储值卡' && _this.is_used == 0){
					_this.MoTaiKuang = true
					uni.request({
						url: _this.httpUrl + 'v_shop_card_infoController/find.xsh',
						method: 'POST',
						header:{ 'content-type':'application/x-www-form-urlencoded' },
						data:{
							shop_pkid:_this.shop_pkid,
							mct_pkid:_this.mct_pkid
						},
						success(res) {
						
							let data = res.data.obj;
							console.log("555555储值卡",data)
							for(let i=0; i<data.length; i++){
								let chooseCardLeftBot = data[i].mctd_give_money + data[i].mctd_money
								_this.chooseCardType.push({
									chooseCardLeftBot:chooseCardLeftBot,
									cCt_created_by:data[i].created_by,
									cCt_created_date:data[i].created_date,
									cCt_mct_name:data[i].mct_name,
									cCt_mct_pkid:data[i].mct_pkid,
									cCt_mctd_discount:data[i].mctd_discount,
									cCt_mctd_frequency:data[i].mctd_frequency,
									cCt_mctd_give_money:data[i].mctd_give_money,
									cCt_mctd_img:data[i].mctd_img,
									cCt_mctd_money:data[i].mctd_money,
									cCt_mctd_month:data[i].mctd_month,
									cCt_mctd_name:data[i].mctd_name,
									cCt_mctd_pkid:data[i].mctd_pkid,
									cCt_mctd_royalty:data[i].mctd_royalty,
									cCt_updated_by:data[i].updated_by,
									cCt_updated_date:data[i].updated_date
								})
							}
							
						}
					})
					console.log("_this.MoTaiKuang",_this.MoTaiKuang)
				}else if(_this.mct_name == '打折卡' && _this.is_used == 0){
					_this.MoTaiKuang = true
					uni.request({
						url: _this.httpUrl + 'v_shop_card_infoController/find.xsh',
						method: 'POST',
						header:{ 'content-type':'application/x-www-form-urlencoded' },
						data:{
							shop_pkid:_this.shop_pkid,
							mct_pkid:_this.mct_pkid
						},
						success(res) {
							let data = res.data.obj
							console.log("555555打折卡",data)
								
								let cardleveldiscount = _this.card_level_discount;
							
							for(let i=0; i<data.length; i++){
							
								let mctddiscount = data[i].mctd_discount;
								
								if(cardleveldiscount == data[i].mctd_discount){
									_this.chooseCardType.push({
										chooseCardLeftBot:data[i].mctd_money,
										cCt_created_by:data[i].created_by,
										cCt_created_date:data[i].created_date,
										cCt_mct_name:data[i].mct_name,
										cCt_mct_pkid:data[i].mct_pkid,
										cCt_mctd_discount:data[i].mctd_discount,
										cCt_mctd_frequency:data[i].mctd_frequency,
										cCt_mctd_give_money:data[i].mctd_give_money,
										cCt_mctd_img:data[i].mctd_img,
										cCt_mctd_money:data[i].mctd_money,
										cCt_mctd_month:data[i].mctd_month,
										cCt_mctd_name:data[i].mctd_name,
										cCt_mctd_pkid:data[i].mctd_pkid,
										cCt_mctd_royalty:data[i].mctd_royalty,
										cCt_updated_by:data[i].updated_by,
										cCt_updated_date:data[i].updated_date
									});
								}
							}
							
						}
					})
					
				}else if(_this.mct_name == '时效卡' && _this.is_used == 0){
					_this.MoTaiKuang = true
					uni.request({
						url: _this.httpUrl + 'v_shop_card_infoController/find.xsh',
						method: 'POST',
						header:{ 'content-type':'application/x-www-form-urlencoded' },
						data:{
							shop_pkid:_this.shop_pkid,
							mct_pkid:_this.mct_pkid
						},
						success(res) {
							let data = res.data.obj
							console.log("555555时效卡",data)
							
							for(let i=0; i<data.length; i++){
								_this.chooseCardType.push({
									chooseCardLeftBot:data[i].mctd_money,
									cCt_created_by:data[i].created_by,
									cCt_created_date:data[i].created_date,
									cCt_mct_name:data[i].mct_name,
									cCt_mct_pkid:data[i].mct_pkid,
									cCt_mctd_discount:data[i].mctd_discount,
									cCt_mctd_frequency:data[i].mctd_frequency,
									cCt_mctd_give_money:data[i].mctd_give_money,
									cCt_mctd_img:data[i].mctd_img,
